.App{
  width: 100%;
  height: 100vh;
  background: cyan;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container{
  display:grid;
  grid-template-columns: 300px;
  grid-template-rows: 300px 80px 40px;
  background-color:yellow;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  scroll-behavior: smooth;
}
h1{
  position: relative;
  top: -330%;
  color: white;
  background: transparent;
  background-color: tomato;
  padding: 6px 0 2px 0;
  margin-bottom: 0px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.time{
  position: relative;
  display: flex;
  top: -440px;
  text-align:start;
  font-size: 25px;
  color: tomato;
}
.day{
  position: relative;
  text-align:end;
  font-size: 25px;
  margin-top: -3%;
  top: -483px;
  color: tomato;
}
input{
  position: relative;
  justify-content: center;
  text-align: center;
  height: 30px;
  left: 50px;
  top: -400px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid tomato;
  outline: none;
}
button{
  position: relative;
  left: 80%;
  top: -440px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: red;
  color: white;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
button:hover{
  background-color: royalblue;

}
ol{
  margin-top:-400px;
}
ol li{
  position: relative;
  left: -20px;
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  font-family:'Times New Roman', Times, serif;
  color: green;
  text-transform:capitalize;
}
.style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.style .fa-times{
  position: relative;
  top: 0px;
  left: -30px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: orange;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.style .fa-times:hover{
  background-color: crimson;
}
.style .fa-times:hover .list_style{
  color:crimson;
}

.style .fa-times{
  margin: o 15px 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
button{
  margin-left: 5px;
  min-height: 40px;
  width: 40px;
  color: white;
  font-size: 30px;
}